import React, { useState, useEffect } from 'react';
import Header from './components/Header';
import MessageCard from './components/MessageCard';
import Footer from './components/Footer';
import CategoryFilter from './components/CategoryFilter';
import FavoritesList from './components/FavoritesList';
import { messageService } from './services/api';
import { useMessageCounter } from './hooks/useMessageCounter';
import './App.css';

function App() {
  const [currentMessage, setCurrentMessage] = useState(null);
  const [loading, setLoading] = useState(false);
  const [categories, setCategories] = useState([]);
  const [selectedCategory, setSelectedCategory] = useState('all');
  const [showFavorites, setShowFavorites] = useState(false);
  const [recentMessageIds, setRecentMessageIds] = useState([]);
  const [stats, setStats] = useState(null);
  const [todayMode, setTodayMode] = useState(false);
  const { count, incrementCounter } = useMessageCounter();

  //      
  useEffect(() => {
    const initializeApp = async () => {
      setLoading(true);
      try {
        //   
        const [message, categoryList, statsData] = await Promise.all([
          messageService.getTodayMessage(),
          messageService.getCategories(),
          messageService.getStats()
        ]);

        setCurrentMessage(message);
        
        // API     CategoryFilter   
        const categoryArray = categoryList.categories || categoryList || [];
        const formattedCategories = categoryArray.map(cat => ({
          name: cat === '' ? 'all' : cat,
          name_ko: cat,
          message_count: statsData?.by_category?.[cat] || 0
        }));
        setCategories(formattedCategories);
        
        setStats(statsData);
        setTodayMode(true);
        incrementCounter();
      } catch (error) {
        console.error('   :', error);
        //    
        try {
          const fallbackMessage = await messageService.getRandomMessage();
          setCurrentMessage(fallbackMessage);
          incrementCounter();
        } catch (fallbackError) {
          console.error('   :', fallbackError);
        }
      }
      setLoading(false);
    };

    initializeApp();
  }, [incrementCounter]);

  const fetchNewMessage = async () => {
    setLoading(true);
    try {
      const filters = {};
      if (selectedCategory && selectedCategory !== 'all') {
        filters.category = selectedCategory;
      }
      
      //     ( )
      if (recentMessageIds.length > 0) {
        filters.excludeIds = recentMessageIds;
      }
      
      const message = await messageService.getRandomMessage(filters);
      
      if (message && message.id) {
        setCurrentMessage(message);
        setTodayMode(false); //   
        incrementCounter();
        
        //    ID  ( 20 )
        setRecentMessageIds(prev => {
          const newIds = [message.id, ...prev.filter(id => id !== message.id)].slice(0, 20);
          return newIds;
        });
      }
    } catch (error) {
      console.error('   :', error);
    }
    setLoading(false);
  };

  const fetchTodayMessage = async () => {
    setLoading(true);
    try {
      const message = await messageService.getTodayMessage();
      setCurrentMessage(message);
      setTodayMode(true);
    } catch (error) {
      console.error('   :', error);
    }
    setLoading(false);
  };

  const handleShare = () => {
    //       
  };

  const handleSpeak = () => {
    //        
  };

  const handleFavorite = async (message) => {
    try {
      //   ID  (    )
      const userId = localStorage.getItem('userId') || 'anonymous_' + Date.now();
      if (!localStorage.getItem('userId')) {
        localStorage.setItem('userId', userId);
      }

      //    ()
      const favorites = JSON.parse(localStorage.getItem('favorites') || '[]');
      const isAlreadyFavorite = favorites.some(fav => fav.id === message.id);
      
      if (!isAlreadyFavorite) {
        favorites.push(message);
        localStorage.setItem('favorites', JSON.stringify(favorites));
        
        //   
        try {
          await messageService.addFavorite(userId, message.id);
        } catch (serverError) {
          console.warn('   ,  :', serverError);
        }
        
        alert(' ! ');
      } else {
        alert('   .');
      }
    } catch (error) {
      console.error('  :', error);
      alert('  .');
    }
  };

  return (
    <div className="min-h-screen gradient-bg">
      <div className="container mx-auto px-4 py-8 max-w-4xl">
        <Header 
          messageCount={count} 
          stats={stats}
          todayMode={todayMode}
          onShowFavorites={() => setShowFavorites(true)}
          onTodayMessage={fetchTodayMessage}
        />
        
        <main className="mb-12">
          <CategoryFilter
            categories={categories}
            selectedCategory={selectedCategory}
            onCategoryChange={setSelectedCategory}
          />
          
          <MessageCard
            message={currentMessage}
            onNewMessage={fetchNewMessage}
            loading={loading}
            todayMode={todayMode}
            onShare={handleShare}
            onSpeak={handleSpeak}
            onFavorite={handleFavorite}
          />
        </main>

        <Footer stats={stats} />
        
        <FavoritesList 
          isOpen={showFavorites}
          onClose={() => setShowFavorites(false)}
        />
      </div>
    </div>
  );
}

export default App;