import { useState, useEffect, useCallback } from 'react';

export const useMessageCounter = () => {
  const [count, setCount] = useState(0);

  //   (  0 )
  useEffect(() => {
    setCount(0);
  }, []);

  //  
  const incrementCounter = useCallback(() => {
    setCount(prev => prev + 1);
  }, []);

  //  
  const resetCounter = useCallback(() => {
    setCount(0);
  }, []);

  return {
    count,
    incrementCounter,
    resetCounter
  };
};